import axios from 'axios';
import moment from 'moment';
import { Toast, Dialog } from 'vant';

moment.locale('zh-cn');

// http request 
axios.timeout = 30000; // (0 )
axios.interceptors.request.use(
    config => {
        config.headers = {
            // 'token': localStorage.getItem('user.token')
        };
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);

// http response 
axios.interceptors.response.use(
    res => {
        // 
        if (res.status === 200) {
            if (res.data.code === 401) {
                // 
                window.location.href = this.GetLoginPageUrl();
            } else if (res.data.code === 403) {
                // 
                window.location.href = '/#/error';
            }
            return Promise.resolve(res.data);
        } else {
            return Promise.reject(res.statusText || '');
        }
    },
    err => {
        // 
        if (err.response.status === 404 || err.response.status === 500 || err.response.status === 304) {
            // let errMsg = err.response.statusText
        }
        return Promise.reject(err.response);
    }
);

class MUtil {
    // 
    request(params) {
        return new Promise((resolve, reject) => {
            axios({
                url: params.url,
                method: params.type || 'get',
                dataType: params.dataType || 'json',
                data: params.data || null
            })
            .then((res) => {
                // 
                if (res.code !== undefined && res.code === 401) {
                    // 401
                    this.doLogin();
                } else {
                    // 
                    resolve(res);
                }
            })
            .catch((err) => {
                // 
                reject(err.statusText || '');
            });
        });
    }
    // GET
    requestGet(url) {
        return new Promise((resolve, reject) => {
            axios.get(url)
            .then((res) => {
                resolve(res);
            })
            .catch((err) => {
                reject(err.statusText || '');
            });
        });
    }
    // POST
    requestPost(url, data) {
        return new Promise((resolve, reject) => {
            axios.post(url, data)
            .then((res) => {
                resolve(res);
            })
            .catch((err) => {
                reject(err.statusText || '');
            });
        });
    }
    // PUT
    requestPut(url, data) {
        return new Promise((resolve, reject) => {
            axios.put(url, data)
            .then((res) => {
                resolve(res);
            })
            .catch((err) => {
                reject(err.statusText || '');
            });
        });
    }
    // DELETE
    requestDelete(url) {
        return new Promise((resolve, reject) => {
            axios.delete(url)
            .then((res) => {
                resolve(res);
            })
            .catch((err) => {
                reject(err.statusText || '');
            });
        });
    }
    GetLoginPageUrl() {
        // return '/Mobile/Index/#/login';
        return '/unLogon/mLogon';
    }
    // 
    doLogin() {
        window.location.href = this.GetLoginPageUrl();
    }
    checkIsLogin() {
        let Cookie = this.getCookie(this.CookieKeys_UserInfo());
        if (!Cookie) {
            this.errorTips('');
            window.location.href = this.GetLoginPageUrl();
        }
    }
    // URL
    getUrlParam(name) {
        // param=123&param1=456
        let queryString = window.location.search.split('?')[1] || '';
        let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
        let result = queryString.match(reg);
        return result ? decodeURIComponent(result[2]) : null;
    }
    // 
    successTips(successMsg) {
        Toast(successMsg || '');
    }
    // 
    errorTips(errMsg) {
        Toast(errMsg || '~');
    }
    // 
    successToast(successMsg) {
        Toast.success(successMsg || '');
    }
    // 
    errorToast(errMsg) {
        Toast.fail(errMsg || '~');
    }
    // 
    successDialog(successMsg) {
        Dialog.alert({
            title: '',
            message: successMsg || ''
        });
    }
    // 
    errorDialog(errMsg) {
        Dialog.alert({
            title: '',
            message: errMsg || '~'
        });
    }
    // 
    setStorage(name, data) {
        let dataType = typeof data;
        // json
        if (dataType === 'object') {
            window.localStorage.setItem(name, JSON.stringify(data));
        } else if (['number', 'string', 'boolean'].indexOf(dataType) >= 0) {
            // 
            window.localStorage.setItem(name, data);
        } else {
            // 
            alert('');
        }
    }
    // 
    getStorage(name) {
        let data = window.localStorage.getItem(name);
        return data || '';
    }
    // 
    removeStorage(name) {
        window.localStorage.removeItem(name);
    }
    formatTime(originTime) {
        if (!originTime) return '';
        return moment(originTime).format('YYYY-MM-DD HH:mm:ss');
    }
    formatDate(originTime) {
        if (!originTime) return null;
        return moment(originTime).format('YYYY-MM-DD');
    }
    formatStrDate(originTime) {
        if (!originTime) return null;
        return moment(new Date(originTime)).format('YYYY-MM-DD');
    }
    formatMonth(originTime) {
        if (!originTime) return null;
        return moment(originTime).format('YYYY-MM');
    }
    formatYear(originTime) {
        if (!originTime) return null;
        return moment(originTime).format('YYYY');
    }
    // truefalse
    checkValueIsExistArray(arr = [], obj) {
        let i = arr.length;
        while (i--) {
            if (arr[i] === obj) {
                return true;
            }
        }
        return false;
    }
    CookieKeys_UserInfo() {
        return 'HR_E97D1714-33A6-4D33-80C7-F18B1A60E624';
    }
    // URL
    GetEmployeeSelfHelpUrl() {
        return '/sub-menu/b2ba1a15-6ef5-4ad6-b732-3bca5318ab8c';
    }
    getCookie(Name) {
        let search = Name + "="; // 
        let returnValue = "";//
        if (document.cookie.length > 0) {
            let sd = document.cookie.indexOf(search);
            let end = '';
            if (sd !== -1) {
                sd += search.length;
                end = document.cookie.indexOf(";", sd);
                if (end === -1)
                    end = document.cookie.length;
                returnValue = unescape(document.cookie.substring(sd, end))
            }
        }
        return returnValue;
    }
    formatMoney(number, places, symbol, thousand, decimal) {
        number = number || 0;
        places = !isNaN(places = Math.abs(places)) ? places : 2;
        symbol = symbol !== undefined ? symbol : "$";
        thousand = thousand || ",";
        decimal = decimal || ".";
        var negative = number < 0 ? "-" : "",
            i = parseInt(number = Math.abs(+number || 0).toFixed(places), 10) + "",
            j = (j = i.length) > 3 ? j % 3 : 0;
        return symbol + negative + (j ? i.substr(0, j) + thousand : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousand) + (places ? decimal + Math.abs(number - i).toFixed(places).slice(2) : "");
    }
}

export default MUtil;



