<template>
    <div class="AnnualLeaveList-box">
        <jd-header title="" :backUrl="backUrl"></jd-header>
        <van-tabs v-model="active">
            <van-tab title=""></van-tab>
            <van-tab title=""></van-tab>
        </van-tabs>
        <van-cell-group v-if="from.DepartName">
            <van-cell>
                <van-row gutter="10">
                    <van-col :span="12"></van-col>
                    <van-col>{{from.DepartName}}</van-col>
                </van-row>
            </van-cell>
            <van-cell>
                <van-row gutter="10">
                    <van-col :span="12"></van-col>
                    <van-col>{{from.FullName}}</van-col>
                </van-row>
            </van-cell>
            <van-collapse v-model="activeNamesJiaBan" v-if="active === 0">
                <van-collapse-item title="" name="1">
                    <van-cell>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.JiaBanLastYear}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.JiaBanTiaoXiuLastYear}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.JiaBanFaQianLastYear}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.JiaBanShengYuLastYear}} </van-col>
                        </van-row>
                    </van-cell>
                </van-collapse-item>
                <van-collapse-item title="" name="2">
                    <van-cell>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.JiaBanTiaoXiu}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.JiaBanFaQian}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.JiaBanShengYu}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.JiaBanShengYuLastYear + from.JiaBanShengYu}} </van-col>
                        </van-row>
                    </van-cell>
                </van-collapse-item>
            </van-collapse>
            <van-collapse v-model="activeNamesYearRest" v-if="active === 1">
                <van-collapse-item title="" name="1">
                    <van-cell>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.LastNianjia}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.LastNianjiaYixiu}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.LastNianjiaShengYu}} </van-col>
                        </van-row>
                    </van-cell>
                </van-collapse-item>
                <van-collapse-item title="" name="2">
                    <van-cell>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.Nianjia}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.NianjiaYixiu}} </van-col>
                        </van-row>
                        <van-row gutter="10">
                            <van-col :span="12"></van-col>
                            <van-col>{{from.NianjiaShengYu}} </van-col>
                        </van-row>
                    </van-cell>
                </van-collapse-item>
            </van-collapse>
        </van-cell-group>
        <van-loading class="loading-box" v-else />
    </div>
</template>

<script>
    import {mapActions, mapGetters} from 'vuex';
    import header from '../../components/common/header.vue';
    import MUtil from '../../util/mm.js';

    const _mm = new MUtil();

    export default {
        components: {
            [header.name]: header
        },
        data() {
            return {
                backUrl: _mm.GetEmployeeSelfHelpUrl(),
                active: 0,
                activeNamesJiaBan: [],
                activeNamesYearRest: [],
                from: {}
            }
        },
        methods: {
            ...mapActions([
                'GetPersonWorkAnnualLeaveList',
                'GetUserInfo',
            ]),
        },
        mounted() {
            _mm.checkIsLogin();
            this.GetPersonWorkAnnualLeaveList().then((res) => {
                let userStr = _mm.getStorage('user.info');
                if (!userStr) {
                    this.GetUserInfo().then((user) => {
                        _mm.setStorage('user.info', user);
                        this.from = Object.assign({}, this.from, res.data, {
                            DepartName: user.DepartName,
                            FullName: user.FullName
                        });
                    });
                } else {
                    let user = JSON.parse(userStr);
                    this.from = Object.assign({}, this.from, res.data, {
                        DepartName: user.DepartName,
                        FullName: user.FullName
                    });
                }
            });
        }
    }
</script>

<style lang="less" scoped>
    @import "../../style/pages/AnnualLeave/AnnualLeaveList";
</style>



